/* LoginModal.css */

/* 모달 배경 스타일 */
#loginModal-wrap::backdrop {
  background-color: rgba(0, 0, 0, 0.7); /* 배경을 더 어둡게 변경 */
}

/* 모달 스타일 */
#loginModal-wrap {
  border: none;
  border-radius: 10px;
  padding: 20px;
  background-color: #2a2f2d; /* 배경색을 더 눈에 띄게 변경 */
  color: #e1eeeb;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.3);
  max-width: 500px;
  width: 100%; /* 너비를 300px로 설정 */
  margin: auto;
}

/* 모달 내용 스타일 */
#loginModal-wrap .modalContent {
  margin-bottom: 20px;
}

/* 버튼 스타일 */
#loginModal-wrap button {
  background-color: #ffaa00;
  border: none;
  border-radius: 5px;
  color: #303b37;
  font-size: 16px;
  padding: 10px 20px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  margin-right: 10px;
}

#loginModal-wrap button:last-child {
  background-color: #303b37;
  color: #e1eeeb;
}

#loginModal-wrap button:hover {
  background-color: #ff8800;
}

/* 입력 필드 스타일 */
#loginModal-wrap input {
  width: 100%;
  padding: 10px;
  margin-top: 10px;
  border: 1px solid #555;
  border-radius: 5px;
  background-color: #303b37;
  color: #e1eeeb;
}

#loginModal-wrap .modalContent{
  width: 480px;
}

